import { mapGetters, mapMutations } from 'vuex'
// 引入子表和通用相关组件
import {
  commonChildTable,
  treeGridChildTable,
  treeGridChildTableCustomize,
  FormAction,
  FormRow
} from 'components/index.js'

// http api
import {
  FormLoad,
  FormInit,
  FormSave,
  FormData,
  FlowAction,
  APIMessages,
  Exec
} from 'api/index.js'

import { commonComponentMixin, formComponentMixin } from 'common/js/mixin.js'
import {
  formatFormAllConfig,
  formatFromDataToView,
  formatFromDataToSave,
  organizeParams,
  organizeSwitchsData,
  settingActionPermission,
  getTabShowRight
} from 'common/js/Util.js'

import { Base64 } from 'js-base64'

const DIRICTION_H = 'horizontal'
const debug = process.env.NODE_ENV !== 'production'

export default {
  name: 'h5Form',
  mixins: [commonComponentMixin, formComponentMixin],
  data () {
    return {
      currentIndex: 0,
      switches: [],
      tabUnitWidth: 100,
      mainformData: {},
      KeyWord: '',
      openformid: '',
      lineHeight: 1.5,
      direction: DIRICTION_H,
      computedWidth: {
        width: '400px'
      },
      formAllConfig: {},
      keywordright: {},
      workflowdata: {},
      afterSaveMsg: '',
      mx_isLoading: false,
      mx_message: '',
      mx_alertShow: false,
      mx_alertTitle: '提示',
      mx_toastShow: false,
      mx_deleyTime: 1000
    }
  },
  computed: {
    switcheWidth () {
      return this.tabUnitWidth * this.switches.length + 'px'
    },
    ...mapGetters([
      'formStatus',
      'routerParams',
      'formStatusValue'
    ])
  },
  created () {
    const { params } = this.$router.history.current

    this.probeType = 1
    this.setFormStatus(params.formstate)
    this.setRouterParams(params)
  },
  mounted () {
    this.$nextTick(() => {
      if (this.timer) {
        clearTimeout(this.timer)
      }

      this.timer = setTimeout(() => {
        this.mainLoad()
      }, 500)
    })
  },
  methods: {
    // 加载数据
    mainLoad () {
      this._FormInit(() => {
        this._FormMainLoad()

        let formstate = this.routerParams.formstate
        if (formstate !== 'add') {
          this._FormData()
        }
      })
    },
    // 保存主表
    saveFromData (callback, msg) {
      let KeyWord = this.KeyWord
      let { formstate } = this.routerParams
      let objItem = formatFromDataToSave(
        this.formAllConfig.comboboxdata,
        this.mainformData,
        KeyWord
      )
      let obj = {
        KeyWord: KeyWord,
        formAllConfig: this.formAllConfig,
        KeyWordType: 'BO',
        data: [objItem],
        formDate: formstate,
        FormId: this.routerParams.FromId
      }

      let params = organizeParams(obj)

      this.MinXinHttpFetch(FormSave(params), (response) => {
        if (response.success) {
          if (!msg) {
            this.afterSaveMsg = '保存数据完成'
          } else {
            this.afterSaveMsg = msg
          }

          this.mx_toastShow = true
          if (callback) {
            callback()
          }
          if (formstate === 'add') {
            this.$router.back()
          }
        }
      })
    },
    // 从获取的配置信息赋予当前表单
    setFormDataFromConfig (formConfigUI) {
      let formTitle = formConfigUI.formTitle

      document.title = formTitle || '表单详情'
      this.switches = organizeSwitchsData(formConfigUI)
      this.tabUnitWidth = 140
    },
    // 获取表单的配置信息
    _FormInit (callback) {
      let KeyValue = this.routerParams.Id
      let formstate = this.routerParams.formstate
      if (formstate === 'add') {
        KeyValue = ''
      }

      if (!this.routerParams.FromId) {
        this.$nextTick(() => {
          this.MixinAlertShowEvent('openformid is ' + this.routerParams.FromId)
        })
      } else {
        let params = {
          FormId: this.routerParams.FromId,
          KeyValue: KeyValue || '',
          FormState: formstate
        }

        this.MinXinHttpFetch(FormInit(params), (response) => {
          if (response.success) {
            let obj = Object.assign({}, response.data)
            this.formAllConfig = formatFormAllConfig(obj)

            this.keywordright = this.formAllConfig.keywordright
            this.workflowdata = this.formAllConfig.workflowdata

            let appconfig = this.formAllConfig.formconfig.appconfig

            if (appconfig) {
              this.KeyWord = appconfig.KeyWord
              this.setFormDataFromConfig(appconfig)
              if (callback) {
                callback()
              }
            } else {
              let { FromId, formstate, Id } = this.routerParams
              if (debug) {
                this.$nextTick(() => {
                  this.MixinAlertShowEvent('获取不到App的配置信息')
                })
              } else {
                this.openPCForm(FromId, formstate, Id)
              }
            }
          }
        })
      }
    },
    // 打开PC端的表单
    openPCForm (FromId, formstate, Id) {
      let baseLink = '/Form/ValidForm/650540a6-af98-4669-b1c3-925691912d79'
      let query = `?FormId=${FromId}&KeyValue=${Id}`
      let link = `${baseLink}/${formstate}/${Id}/${Base64.encode(FromId)}${query}`

      window.open(link, '_self')
    },
    // 判断tab的显示方法
    checkSwitchPermission (formConfigUI, formData, value, subPermission) {
      let switchsPermission = formConfigUI.switchsPermission
      let switchs = formConfigUI.switchs
      let formstate = this.routerParams.formstate

      this.tabUnitWidth = formConfigUI.tabUnitWidth

      switchs = getTabShowRight(switchs, switchsPermission, formData, formstate, value, subPermission)
      this.switches = organizeSwitchsData(switchs, formConfigUI)

      this.timer = setTimeout(() => {
        this.$refs.navBarScroll.refresh()
      }, 200)
    },
    // 加载主表数据
    _FormMainLoad () {
      let KeyValue = this.routerParams.Id
      let formstate = this.routerParams.formstate
      if (formstate === 'add') {
        KeyValue = ''
      }

      let params = {
        KeyWord: this.KeyWord,
        KeyValue: KeyValue,
        KeyWordType: 'BO',
        swhere: '',
        select: '',
        formstate: formstate
      }

      this.MinXinHttpFetch(FormLoad(params), (response) => {
        if (response.success) {
          let statusField = this.formAllConfig.formconfig.config.joindata.statusfield
          let value = response.data.value
          let getData = JSON.parse(value)
          let Status = getData[statusField]

          this.setFormStatusValue(Status)

          this.actionMenu = settingActionPermission(this.workflowdata, formstate, Status)
          if (value || value !== '') {
            let mainformData = formatFromDataToView(
              this.formAllConfig.comboboxdata,
              getData,
              this.KeyWord
            )

            this.mainformData = Object.assign({}, mainformData)
          }
        }
      })
    },
    // 获取额外的数据
    _Exec (switchsPermission, mainformData, callback) {
      let params = Object.assign({}, switchsPermission.condition.dataParams)
      let field = switchsPermission.field

      params.MethodParams[field] = mainformData[field]
      this.MinXinHttpFetch(Exec(JSON.stringify(params)), (response) => {
        if (response.success) {
          let value = response.data.value
          if (callback) {
            callback(value)
          }
        } else {
          if (callback) {
            callback()
          }
          this.MixinAlertShowEvent(response.message)
        }
      })
    },
    // 获取表单的所有子表信息
    _FormData () {
      let params = {
        FormId: this.routerParams.FromId,
        KeyValue: this.routerParams.Id,
        extparams: ''
      }

      this.MinXinHttpFetch(FormData(params), (response) => {
        if (response.success) {
          let getData = response.data.value
          let childrenData = [...getData.children]
          childrenData.forEach((item, index) => {
            if (!item.values) {
              item.values = []
            }

            this.switches.forEach((switchItem, switchIndex) => {
              if (switchItem.KeyWord === item.KeyWord) {
                switchItem.data = Object.assign({}, item)
              }
            })
          })
        }
      })
    },
    // 走流程之前都要更新的数据
    upDateForm (callback, msg) {
      let KeyWord = this.KeyWord
      let formDate = this.routerParams.formstate
      let objItem = formatFromDataToSave(
        this.formAllConfig.comboboxdata,
        this.mainformData,
        KeyWord
      )
      let obj = {
        KeyWord: KeyWord,
        formAllConfig: this.formAllConfig,
        KeyWordType: 'BO',
        data: [objItem],
        formDate: formDate,
        FormId: this.routerParams.FromId
      }

      let params = organizeParams(obj)
      let roleParams = {}

      if (this.workflowdata && formDate === 'view') {
        roleParams.IsWorkFlowHuman = '1'
      } else if (formDate === 'add') {
        roleParams.IsRegHuman = '1'
      }

      let upDateParams = {
        OpenTrans: 'true',
        Update: {
          MessageCode: 'Power.Controls.PMS.SaveWebForm',
          data: {
            formId: params.FormId,
            json: JSON.parse(params.JsonData),
            params: Base64.encode(JSON.stringify(roleParams)),
            FlowOperate: 'Update'
          }
        }
      }

      this.MinXinHttpFetch(APIMessages(JSON.stringify(upDateParams)), (response) => {
        if (response.success) {
          if (!msg) {
            this.afterSaveMsg = '数据更新完成'
          } else {
            this.afterSaveMsg = msg
          }

          this.mx_toastShow = true
          if (callback) {
            callback()
          }
        }
      })
    },
    // selectAction中的驱动事件
    selectActionMenu (item) {
      let { formstate, Id, FromId } = this.routerParams

      // 如果点击取消，返回false
      if (!item) {
        return false
      }

      // 执行保存
      if (item.value && item.value === 'SaveForm') {
        this.saveFromData(() => {
          this.mainLoad()
        })
      }

      // 执行送审
      if (item.value && item.value === 'Active') {
        this.upDateForm(() => {
          let query = {
            KeyWord: this.KeyWord,
            KeyValue: Id,
            FormId: FromId,
            flowOperate: 'Active',
            SequeID: this.workflowdata.CanFlowOperate.SequeID,
            FormState: formstate
          }

          this.$router.push({
            path: '/forkflow/active',
            query: query
          })
        }, '更新数据完毕')
      }

      // 执行同意
      if (item.value && item.value === 'Send') {
        this.upDateForm(() => {
          let query = {
            KeyWord: this.KeyWord,
            KeyValue: Id,
            FormId: FromId,
            flowOperate: 'Send',
            SequeID: this.workflowdata.CanFlowOperate.SequeID,
            WorkInfoID: this.workflowdata.CanFlowOperate.CanFlowList.ShowMonitor.WorkInfoID,
            FormState: formstate
          }

          this.$router.push({
            path: '/forkflow/agree',
            query: query
          })
        }, '更新数据完毕')
      }

      // 执行回收
      if (item.value && item.value === 'GetBack') {
        this.upDateForm(() => {
          let params = Object.assign({}, {
            Current: {
              KeyWord: this.KeyWord,
              KeyValue: Id,
              FormId: FromId,
              SequeID: this.workflowdata.CanFlowOperate.SequeID + '',
              FormState: formstate
            },
            FlowOperate: 'GetBack'
          })

          this.MinXinHttpFetch(FlowAction(JSON.stringify(params)), (response) => {
            if (response.success) {
              this.mainLoad()
            }
          })
        }, '更新数据完毕')
      }

      // 执行查看监控 不需要更新数据
      if (item.value && item.value === 'ShowMonitor') {
        let query = {
          flowOperate: 'ShowMonitor',
          WorkInfoID: this.workflowdata.CanFlowOperate.CanFlowList.ShowMonitor.WorkInfoID
        }

        this.$router.push({
          path: '/forkflow/monitor',
          query: query
        })
      }

      // 执行驳回
      if (item.value && item.value === 'Return') {
        this.upDateForm(() => {
          let query = {
            KeyWord: this.KeyWord,
            KeyValue: Id,
            FormId: FromId,
            SequeID: this.workflowdata.CanFlowOperate.SequeID,
            FormState: formstate
          }

          this.$router.push({
            path: '/forkflow/return',
            query: query
          })
        }, '更新数据完毕')
      }

      // 执行委派
      if (item.value && item.value === 'Delegate') {
        this.upDateForm(() => {
          let query = {
            KeyWord: this.KeyWord,
            KeyValue: Id,
            FormId: FromId,
            SequeID: this.workflowdata.CanFlowOperate.SequeID,
            FormState: formstate
          }

          this.$router.push({
            path: '/forkflow/delegate',
            query: query
          })
        }, '更新数据完毕')
      }

      // 委派处理
      if (item.value && item.value === 'Delegateing') {
        this.upDateForm(() => {
          let query = {
            KeyWord: this.KeyWord,
            KeyValue: Id,
            FormId: FromId,
            SequeID: this.workflowdata.CanFlowOperate.SequeID,
            FormState: formstate
          }

          this.$router.push({
            path: '/forkflow/delegateing',
            query: query
          })
        }, '更新数据完毕')
      }

      // 终止
      if (item.value && item.value === 'Stop') {
        this.upDateForm(() => {
          let query = {
            KeyWord: this.KeyWord,
            KeyValue: this.routerParams.Id,
            FormId: this.routerParams.FromId,
            SequeID: this.workflowdata.CanFlowOperate.SequeID,
            FormState: formstate
          }

          this.$router.push({
            path: '/forkflow/stop',
            query: query
          })
        }, '更新数据完毕')
      }

      // 查看历史
      if (item.value && item.value === 'ShowHistoryMonitor') {
        let query = {
          KeyWord: this.KeyWord,
          KeyValue: this.routerParams.Id,
          FormId: this.routerParams.FromId,
          SequeID: this.workflowdata.CanFlowOperate.SequeID,
          FormState: formstate
        }

        this.$router.push({
          path: '/forkflow/history',
          query: query
        })
      }
    },
    // 检测数据修改动态
    formRowChange (item) {
      this.mainformData[item.field] = item.value
    },
    switchItem (index) {
      this.currentIndex = index
    },
    _isShow (index) {
      return this.currentIndex === index
    },
    ...mapMutations({
      setFormStatus: 'SET_FORM_STATUS',
      setRouterParams: 'SET_ROUTER_PARAMS',
      setFormStatusValue: 'FORMSTATUSVALUE'
    })
  },
  components: {
    FormRow,
    FormAction,
    commonChildTable,
    treeGridChildTable,
    treeGridChildTableCustomize
  }
}



